import Sidebar from "../Sidebar";
import Navbar from "../Navbar";
import {Form,Button,Alert} from 'react-bootstrap';
import React, { useState } from "react";
import BookDataService from "../services/book.services";

export const Tables = () => {
  const [arenaType, setArenaType] = useState("");
  const [fieldName, setFieldName] = useState("");
  const [capacity, setCapacity] = useState("");
  const [price, setPrice] = useState("");
  const [ownerName, setOwnerName] = useState("");
  const [phoneNo, setPhoneNo] = useState("");
  const [email, setEmail] = useState("");
  const [details, setDetails] = useState("");
 
  const [message, setMessage] = useState({ error: false, msg: "" });
 
  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage("");
    if (arenaType === "" || capacity === "" || fieldName === "" || price === "" ||ownerName === "" || phoneNo === "" || email === "" || details === "" ) {
      setMessage({ error: true, msg: "All fields are mandatory!" });
      return;
    }

    const newBook = {
      arenaType,
      fieldName,
      capacity,
      price,
      ownerName,
      phoneNo,
      email,
      details,
    };

    console.log(newBook);

    try {
        await BookDataService.addBooks(newBook);
        setMessage({ error: false, msg: "New Book added successfully!" });
      }
     catch (err) {
      setMessage({ error: true, msg: err.message });
    }

    setArenaType("");
    setCapacity("");
    setFieldName("");
    setPrice("");
    setOwnerName("");
    setPhoneNo("");
    setEmail("");
    setDetails("");

  };

  return (
    <div className="d-flex">
      <div>
        <Sidebar/>
      </div>
      <div style={{flex:"1 1 auto", display:"flex", flexFlow:"column", height:"100vh", overflowY:"hidden"}}>
        <Navbar/>
        <div style={{height:"100%"}}>
          <div style={{padding:"20px 5%",height:"calc(100% - 64px)",overflowY:"scroll"}}>
            <div style={{display:"grid", gridTemplateColumns:"repeat(1, minmax(200px, 700px))"}}>
          {/* ///////////////Form is Stating /////////  */}

          {message?.msg && (
          <Alert
            variant={message?.error ? "danger" : "success"}
            dismissible
            onClose={() => setMessage("")}
          >
            {message?.msg}
          </Alert>
        )}

            <Form onSubmit={handleSubmit}>
            <div class="container">
            <div class="row">
              <div class="col align-self-start">
              <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label class="font-weight-bold">Arena Type </Form.Label>
                  <Form.Control type="text" placeholder="Enter Type"  value={arenaType}
                onChange={(e) => setArenaType(e.target.value)}/>
                </Form.Group>

                         
          <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label class="font-weight-bold">Capacity </Form.Label>
                  <Form.Control type="number" placeholder="Enter Capasity"  value={capacity}
                onChange={(e) => setCapacity(e.target.value)}/>
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label class="font-weight-bold">Owner Name </Form.Label>
                  <Form.Control type="text" placeholder="Enter Name" value={ownerName}
                onChange={(e) => setOwnerName(e.target.value)}/>
                </Form.Group>

              </div>

              <div class="col align-self-center">
              <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label class="font-weight-bold">Field Name </Form.Label>
                  <Form.Control type="text" placeholder="Enter Name" value={fieldName}
                onChange={(e) => setFieldName(e.target.value)}  />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label class="font-weight-bold">Price Per Hour</Form.Label>
                  <Form.Control type="number" placeholder="Enter Price" value={price}
                onChange={(e) => setPrice(e.target.value)} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label class="font-weight-bold">Phone No </Form.Label>
                  <Form.Control type="number" placeholder="Enter Phone" value={phoneNo}
                onChange={(e) => setPhoneNo(e.target.value)}/>
                </Form.Group>
              </div>
            </div>
          </div>
          <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label class="font-weight-bold">Email address</Form.Label>
                <Form.Control type="email" placeholder="Enter email" value={email}
                onChange={(e) => setEmail(e.target.value)}/>
              </Form.Group>

          <div class="form-group">
              <label for="exampleFormControlTextarea1" class="font-weight-bold">Details</label>
              <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"  placeholder="Enter details" value={details}
                onChange={(e) => setDetails(e.target.value)}></textarea>
            </div>
              
              <div className="m-3">
                <label className="mx-3" class="font-weight-bold">Upload Image:</label>
                    <div>
              
                {/* <div>
                    <input accept="image/*"  type="file"  onChange={(e)=>
            setGrounddata({...Grounddata,file:e.target.value})} required multiple/>
                </div>  */}
                </div>
                </div> 
               
              <Button variant="primary" type="Submit" >
                ADD ARENA
              </Button>
          </Form>
            </div>  
          </div>
        </div>
      </div>
    </div>
  );
};
