import React from "react";
import { 
  CDBSidebar,
  CDBSidebarContent,
  CDBSidebarFooter,
  CDBSidebarHeader,
  CDBSidebarMenu,
  CDBSidebarMenuItem } from "cdbreact";
import { NavLink } from "react-router-dom";

const Sidebar = () => {

  return (
    <div
      className={`app`}
      style={{ display: "flex", height: "100%", overflow:"scroll initial"}}
    >
      <CDBSidebar
        textColor="#fff"
        backgroundColor="#333"
      >
        <CDBSidebarHeader
          prefix={
            <i className="fa fa-bars fa-large"></i>
          }
        >
          <a href="/" className="text-decoration-none" style={{color:"inherit"}}>
            Arena Booking
          </a>
        </CDBSidebarHeader>

        <CDBSidebarContent className="sidebar-content">
          <CDBSidebarMenu>
     {/* Nav link of DAshboard        */}
            <NavLink exact to="/" activeClassName="activeClicked">
              <CDBSidebarMenuItem
                icon="columns"
              >
                Dashboard
              </CDBSidebarMenuItem>
            </NavLink>

        {/* Nav link of Add Ground       */}     
            <NavLink exact to="/tables" activeClassName="activeClicked">   
              <CDBSidebarMenuItem
                icon="campground"
              >
                Add Grounds 
              </CDBSidebarMenuItem>
            </NavLink>
          {/* Nav link of Edit Ground */}
            <NavLink exact to="/profile" activeClassName="activeClicked">
              <CDBSidebarMenuItem
                icon="edit"
              >
                Edit Ground
              </CDBSidebarMenuItem>
            </NavLink>

        {/* Nav link of Delete Ground */}
            <NavLink exact  to="/profile" activeClassName="activeClicked">
              <CDBSidebarMenuItem
                icon="trash-alt"
              >
                Delete Ground
              </CDBSidebarMenuItem>
            </NavLink>

            <NavLink exact  to="/Viewallgrounds" activeClassName="activeClicked">
              <CDBSidebarMenuItem
                icon="futbol"
              >
                View Grounds
              </CDBSidebarMenuItem>
            </NavLink>
            
          </CDBSidebarMenu>
          <CDBSidebarMenu>
          </CDBSidebarMenu>
        </CDBSidebarContent>

        <CDBSidebarFooter style={{ textAlign: "center" }}>
          <div
            className="sidebar-btn-wrapper"
            style={{
              padding: "20px 5px"
            }}
          >
          
          </div>
        </CDBSidebarFooter>
      </CDBSidebar>
    </div>
  );
}

export default Sidebar;
